import axios from 'axios'
/**
 * getget
 * @param {String} url [url]
 * @param {Object} params []
 */
export function get ( url, params ) {
  params = params || {}
  return new Promise( ( resolve, reject ) => {
    axios
      .get( url, {
        params: params
      } )
      .then( res => {
        resolve( res.data )
      } )
      .catch( err => {
        reject( err.data )
      } )
  } )
}
/**
 * postpost
 * @param {String} url [url]
 * @param {Object} params []
 */
export function post(url,params){
  params = params || {}
  return new Promise( ( resolve, reject ) => {
    axios
        .post(url,params)
        .then(res=>{
          resolve(res.data)
        }).catch(err=>{
      reject(err.data())
    })
  })
}
export default axios
